<template>
  <div class="sciense-page">
    <!-- 面包屑 -->
    <p class="crumbs">学术委员信息库</p>
    <!-- 搜索 -->
    <div class="search" style="margin-top:30px;">
      <span class="fl">学术委员会名称：</span>
      <div class="fl" style="marginRight:60px">
        <el-input placeholder="请输入内容" v-model="search2" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" class="searchbtn"></el-button>
        </el-input>
      </div>
      <span class="fl">学术委员会名称：</span>
      <div class="fl">
        <el-input placeholder="请输入内容" v-model="search2" class="input-with-select">
          <el-button slot="append" icon="el-icon-search" class="searchbtn"></el-button>
        </el-input>
      </div>
      <!-- 取消按钮 -->
      <div class="cancel fr">
        <el-button>
          <i class="el-icon-circle-close-outline" style="marginRight:4px;color:#63b4ff;"></i>取消
        </el-button>
      </div>
      <!-- 查询按钮 -->
      <div class="require fr">
        <el-button type="primary" icon="el-icon-search">搜索</el-button>
      </div>
    </div>
    <!-- 表格 -->
    <el-table
      ref="multipleTable"
      :data="tableData3"
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      border
      :header-cell-style="{background:'#EBEBEB'}"
    >
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column label="序号" width="50">
        <template slot-scope="scope">{{ scope.row.num }}</template>
      </el-table-column>
      <!-- show-overflow-tooltip控制超过单元格的内容是否用...隐藏 -->
      <el-table-column prop="pnum" label="委员人员编号" width="100"></el-table-column>
      <el-table-column prop="name" label="姓名" width="60"></el-table-column>
      <el-table-column prop="age" label="年龄" width="50"></el-table-column>
      <el-table-column prop="zaibian" label="在编/外聘" width="80"></el-table-column>
      <el-table-column prop="organization" label="所属组织" width="120"></el-table-column>
      <el-table-column prop="education" label="学历" width="100"></el-table-column>
      <el-table-column prop="level" label="职称" width="100"></el-table-column>
      <el-table-column prop="appraise" label="评价（优秀、合格）" width="130"></el-table-column>
      <el-table-column prop="hireDate" label="聘用日期" width="100"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <!-- 编辑按钮 -->
          <el-button
            size="mini"
            type="primary"
            icon="el-icon-edit"
            @click="handleEditUser(scope.$index, scope.row)"
            circle
          ></el-button>
          <!-- 删除按钮 -->
          <el-button
            size="mini"
            type="danger"
            icon="el-icon-delete"
            @click="handleDelete(scope.$index, scope.row)"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 编辑委员信息 -->
    <el-dialog @closed="handleClosed" title="编辑委员信息" :visible.sync="editUserDialogFormVisible">
      <el-form ref="myform" :rules="formRules" label-width="100px" :model="formData">
        <el-form-item label="委员编号" prop="username">
          <el-input v-model="formData.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="formData.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="年龄">
          <el-input v-model="formData.mobile" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editUserDialogFormVisible = false">取 消</el-button>
        <el-button @click="SureHandleEdit" type="primary">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 分页 -->
    <div class="block fl">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[100, 200, 300, 400]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="400"
      ></el-pagination>
    </div>
    <!-- 删除按钮 -->
    <div class="delete fr">
      <el-button>
        <i class="el-icon-delete" style="marginRight:4px;color:#63b4ff;"></i>删除
      </el-button>
    </div>
    <!-- 申请按钮 -->
    <div class="apply fr">
      <el-button type="primary" icon="el-icon-document">申请</el-button>
    </div>
  </div>
</template>
<script>
/*
 *
 * @author renchao 2019-01-29
 */
export default {
  name: "sciense",
  data() {
    return {
      // 分页
      currentPage: 1,
      // 搜索框
      search1: "",
      search2: "",
      // 表格
      tableData3: [
        {
          num: "1",
          pnum: "11090",
          name: "王小虎",
          age: "40",
          zaibian: "在编",
          organization: "北京医科院",
          education: "博士",
          level: "教授",
          appraise: "优秀",
          hireDate: "2019-01-19"
        }
        // {
        //   num: "2",
        //   name: "王小虎",
        //   address: "上海市普陀区金沙江路 1518 弄"
        // },
        // {
        //   num: "3",
        //   name: "王小虎",
        //   address: "上海市普陀区金沙江路 1518 弄"
        // },
        // {
        //   num: "4",
        //   name: "王小虎",
        //   address: "上海市普陀区金沙江路 1518 弄"
        // }
      ],
      multipleSelection: [],
      // 绑定表单数据
      formData: {
        username: "",
        password: "",
        email: "",
        mobile: ""
      },
      // 表单验证规则
      formRules: {
        username: [
          { required: true, message: "请输入委员姓名", trigger: "blur" },
          { min: 1, max: 5, message: "长度在 1 到 5 个字符", trigger: "blur" }
        ],
        password: [
          { required: true, message: "请输入活动名称", trigger: "blur" },
          { min: 1, max: 5, message: "长度在 1 到 5 个字符", trigger: "blur" }
        ]
      },
      editUserDialogFormVisible: false
    };
  },
  methods: {
    // 操作按钮
    // 编辑委员信息
    handleEditUser() {
      // 显示对话框
      this.editUserDialogFormVisible = true;
      // 显示用户信息

      // this.formData.username = user.username
      // this.formData.email = user.email
      // this.formData.mobile = user.mobile
      // this.formData.id = user.id
    },
    // 确认编辑按钮
    SureHandleEdit() {},
    // 删除委员信息
    handleDelete() {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    // 关闭按钮
    handleClosed() {
      for (const key in this.formData) {
        this.formData[key] = "";
      }
    },
    // 表格
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  }
};
</script>
<style lang="less">
.sciense-page {
  /* 申请按钮 */
  .apply .el-button {
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    background-color: #63b4ff;
  }
  .el-input-group__append,
  .el-input-group__prepend {
    color: #fff;
  }
  /* 删除按钮 */
  .delete .el-button {
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    color: #63b4ff;
    margin-left: 20px;
  }
  /* 分页 */
  .block {
    margin-left: 29%;
  }
  /* 表格 */
  .el-table {
    margin: 30px 0;
  }
  /* 右部分 */
  /* 右部分 */
  .right {
    margin-left: 3%;
    margin-right: 3%;
    margin-top: 20px;
    width: 79.5%;
  }
  .crumbs {
    margin-bottom: 20px;
    font-size: 16px;
    font-weight: 700;
  }
  /* 搜索框 */
  .el-input-group__append button.el-button {
    background-color: #63b4ff;
    border: 0;
    border-radius: 0;
  }
  .search {
    overflow: hidden;
  }
  .search span {
    height: 40px;
    line-height: 40px;
  }
  /* 查询按钮 */
  .require .el-button {
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    background-color: #63b4ff;
  }
  /* 取消按钮 */
  .cancel .el-button {
    height: 40px;
    line-height: 40px;
    padding: 0 20px;
    color: #63b4ff;
    margin-left: 20px;
  }
}
</style>





